import { LOGIN_REQUEST, LOGIN_SUCCESS, LOGIN_FAILED } from "./constant";

let initialState = {
  loading: false,
  data: null,
  err: null,
};

const authReducers = (state = initialState, action) => {
  switch (action.type) {
    case LOGIN_REQUEST:
      state.loading = true;
      state.data = null;
      state.err = null;
      return { ...state };

    case LOGIN_SUCCESS:
      state.loading = false;
      state.data = action.payload;
      state.err = null;
      return { ...state };

    case LOGIN_FAILED:
      state.loading = false;
      state.data = null;
      state.err = action.payload;
      return { ...state };

    default:
      return { ...state };
  }
};

export default authReducers;
